<template>
  <div class="input-search-wrapper">
    <el-input v-model="currentValue" :disabled="Boolean(options.readonly)" ref="input">
      <el-button
        slot="append"
        icon="el-icon-search"
        type="primary"
        :disabled="Boolean(options.readonly)"
        @click="handleClickSearch"
        @newAddress="handleNewAddress"
      ></el-button>
    </el-input>
  </div>
</template>

<script>
export default {
  name: "CInputSearch",
  model: {
    prop: "value",
    event: "input"
  },
  props: {
    value: String,
    options: Object
  },
  mounted() {},
  data() {
    return {
      currentValue: this.value
    };
  },
  computed: {},
  methods: {
    focus() {
      this.$refs.input.focus();
    },
    handleNewAddress(formValue) {
      debugger;
      this.currentValue = JSON.stringify(formValue);
    },
    handleClickSearch() {
      this.$global.getAddressSingleton().open(this);
    },
    update(value) {
      this.$emit("input", value);
    }
  },
  watch: {
    currentValue(newValue) {
      this.$emit("input", newValue);
    }
  },
  components: {}
};
</script>

